import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import { Row, Col } from 'react-bootstrap';
import * as lab from '@material-ui/lab';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import AddIcon from '@material-ui/icons/Add';
import { MenuItem, Input, Checkbox, Icon, TextField } from '@material-ui/core';
import { format } from 'date-fns'

import DateFnsUtils from '@date-io/date-fns'; // choose your lib
import {
  DateTimePicker,
  MuiPickersUtilsProvider,
} from '@material-ui/pickers';


const FormCreate = ({ disabled ,disableStart, disableEnd, options, onValueChange, onAddMore, locationName, definedCode, onDelete }) => {
    const [optSelected, setOptSelected] = useState(options.find(o => o.value === locationName) ?? null);
    const [inputCode, setInputCode] = useState(definedCode ?? '');
    const [searchValue, setSearchValue] = useState('');

    const [dataToSend, setDataToSend] = useState({
        disabled: disabled,
        startDate: disableStart,
        endDate: disableEnd,
        iataCode: definedCode || null,
        iataCodeId: optSelected ? optSelected.value : null });
    
    const TriggerChange = (s) => { 
        console.log(s)
        if ( (s.iataCode !== null && s.iataCode !== undefined) &&
        (s.iataCodeId !== null && s.iataCodeId !== undefined)) {
        onValueChange(s)
    } }

    useEffect(() => {
        TriggerChange(dataToSend)
    }, [dataToSend])

    const children = options.map((item) => (
        <MenuItem key={item.value} value={item.value} label={item.label}>
            {`${item.label} - (${item.value})`}
        </MenuItem>
    ));

    return (
        <>
            <Row>
                <Col>
                    <lab.Autocomplete
                        id="combo-box-demo"
                        inputValue={optSelected? optSelected.label : null}
                        options={options}
                        getOptionLabel={(option) => option.label}
                        renderInput={(params) => <TextField {...params} label="Combo box" variant="outlined" />}
                        onInputChange={(event, value) => {
                            if (!value) return

                            setDataToSend(prev => {
                                const newState = {
                                    ...prev,
                                    iataCodeId: options.find(o => o.label === value).value,
                                }
                                return newState
                            })
                        }}
                    >
                        {children}
                    </lab.Autocomplete>
                </Col>
                <Col xs={2}>
                    <TextField
                        value={inputCode}
                        onChange={(e) => {
                            e.persist();
                            setInputCode(e.target.value)
                            setDataToSend(prev => {
                                const newState = {
                                    ...prev,
                                    iataCode: e.target.value
                                }
                                return newState
                            })
                        }} placeholder="Internal Code" />
                </Col>

                <Col xs={6}>
                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                        <DateTimePicker value={dataToSend.startDate || null} onChange={(d) => {
                            setDataToSend(prev => {
                                const newState = {
                                    ...prev,
                                    startDate: format(d, `yyyy-MM-dd'T'hh:mm:00`),
                                }
                                return newState;
                            })

                        }} />
                        <DateTimePicker value={dataToSend.endDate || null} onChange={(d) => {
                            setDataToSend(prev => {

                                const newState = {
                                    ...prev,
                                    endDate: format(d, `yyyy-MM-dd'T'hh:mm:00`),
                                }
                                return newState;
                            })

                        }} />
                    </MuiPickersUtilsProvider>
                </Col>
                <Col style={{ cursor: 'pointer' }} >
                    <FormControlLabel
                        control={<Checkbox
                            checked={(dataToSend.startDate !== null && dataToSend.startDate !== undefined && dataToSend.endDate !== null && dataToSend.endDate !== undefined) || dataToSend.disabled}
                            onChange={() => {
                                setDataToSend(prev => ({ ...prev, disabled: !prev.disabled }))
                            }} />}
                        label="Disabled"
                    />
                </Col>
                <Col style={{ cursor: 'pointer' }} onClick={onAddMore} >
                    <AddIcon />
                </Col>
                <Col
                    style={{ cursor: 'pointer' }}
                    onClick={() => {
                        if (!optSelected) return
                        Axios({
                            method: 'DELETE',
                            url: `${process.env.REACT_APP_BACKEND_URL || window.location.origin}/iatacodes`,
                            data: { id: optSelected.value },
                        }).then(() => {
                            onDelete()
                                .then(() => {
                                    /*notification['success']({
                                        message: 'Success!',
                                        description: 'Code deleted!'
                                    });*/
                                })
                        })
                    }}
                
                    
                >
                    <DeleteForeverIcon />
                </Col>
            </Row>
        </>
    );
}

export default FormCreate;