import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Button } from '@material-ui/core';
import { iatacodes, clientIatacodes, postCodes } from "../../crud/iatacodes.crud";
import FormCreate from "../../widgets/locationsMap/FormCreate";

export default function MyPage(p) {
    const [locations, setLocations] = useState(null);
    const [counter, setCounter] = useState(1);
    const [valuesToSend, setValuesToSend] = useState([]);
    const [createdLocations, setCreatedLocations] = useState([]);

    useEffect(() => {
        iatacodes().then(r => setLocations(r))

        clientIatacodes()
            .then(r => {
                setCreatedLocations(r)
                setValuesToSend(prev => {
                    return [...prev, ...r.map(v => ({ iataCodeId: v.iatacodeId, iataCode: v.iatacode, disabled: v.disabled, startDate: v.disableStartTime, endDate: v.disableEndTime }))]
                })
            })
    }, [])

    if (!locations) {
        return (<div style={{ flex: '1' }}>
            Loading
        </div>);
    }

    return (
        <Container>
            <Row>
                <Col>
                    {createdLocations.map((c, idx) => {
                        return (<FormCreate
                            key={c.iatacodeId}
                            disabled={c.disabled}
                            onDelete={() => {
                                return Promise.resolve()
                                    .then(r => {
                                        setCreatedLocations(r)
                                    })
                            }}
                            disableStart={c.disableStartTime}
                            disableEnd={c.disableEndTime}
                            locationName={c.iatacodeId}
                            definedCode={c.iatacode}
                            onAddMore={() => setCounter(prev => prev + 1)}
                            options={locations.map(l => ({ value: l.code, label: l.location }))}
                            onValueChange={v => {
                                setValuesToSend(previous => {
                                    const found = previous.find(prev => prev.iataCodeId === v.iataCodeId)
                                    if (!found) return [...previous, v];

                                    return [...previous.filter(f => f.iataCodeId !== found.iataCodeId), v]
                                });
                            }}
                        />);
                    })}
                    {Array(counter).fill(1).map((_, idx) => {
                        return (<FormCreate
                            key={idx}
                            disabled={false}
                            onDelete={() => {

                                return clientIatacodes()
                                    .then(r => {
                                        setCreatedLocations(r)
                                    })
                            }}
                            onAddMore={() => setCounter(prev => prev + 1)}
                            options={locations.map(l => ({ value: l.code, label: l.location }))}
                            onValueChange={v => {
                                console.log(v)

                                setValuesToSend(previous => {
                                    const found = previous.find(prev => prev.iataCodeId === v.iataCodeId)
                                    if (!found) return [...previous, v];
                                    return [...previous.filter(f => f.iataCodeId !== found.iataCodeId), v]
                                });
                            }}
                        />);
                    })}
                </Col>
            </Row>
            <Row>
                <Col>
                    <Button variant="contained" color="primary" onClick={() => {
                        postCodes(valuesToSend)
                        .then(() => clientIatacodes())
                        .then(r => {
                            /*notification['success']({
                                message: 'Success!',
                                description: 'Codes saved on database'
                            });*/
                            setCreatedLocations(r)
                            setCounter(0)
                        })
                    }}>
                        Save
                    </Button>
                </Col>
            </Row>
        </Container>
    );
} 